*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color:bisque ;
}
.main{
    width: 100%;
    height: 100vh;
    margin: auto;
    background-color: bisque;
    display: flex;
    justify-content:center;
    align-items: center;  
}
.container{
    position: relative;
    width:50%;
    background: linear-gradient(#ffffffd3,#d8c7f7);
    height:fit-content;
}
.main-heading{
    position:sticky;
    top:0;
    left:50px;
    color: aliceblue;
    background-color: #000;
    text-align: center;
    padding:10px 0;
    font-size: 1.2rem;
    text-transform: capitalize;
    text-shadow:2px 2px 6px #fff;
}
.question-ans{
    margin: 10px 0;
    background-color: #f1a7a7;
    padding: 10px 0;
    font-size: 1rem;
}

.question{
    display: flex;
    align-items: center;
}
.btn{
    cursor: pointer;
    background-color: green;
    padding: 6px;
    border-radius: 9px;
    margin-left: 10px;
}
.qns{
    margin-left: 40px;
    color:#fff;
    letter-spacing: 1px;
    font-weight: 500;
}
.answer{
    background-color: #fff;
    margin: 20px 0;
    padding: 10px;
    line-height: 20px;
}
.ans{
    font-size: 0.9rem;
    font-weight: 400;
    letter-spacing: 0.4px;
}

@media(max-width:720px){
    .container{
        width:100%;
        height: 100vh;
    }
    .btn{
        padding: 2px;
        border-radius: 4px;
    }
}